  "7c3a832e": (_ctx.tabsBeforAColor),
  "7993c95c": (_ctx.tabsAColor),
  "5905e714": (_ctx.tabsATColor),
  "7993c95c": (_ctx.tabsAColor),
  "5905e714": (_ctx.tabsATColor)
